import React, { useState } from "react";

import classes from "../../assets/css/App.module.css";
import {
  row0,
  row1,
  row2,
  item1,
  item2,
  divider,
} from "../../assets/css/Column.module.css";

import TextField from "@material-ui/core/TextField";
import Fab from "@material-ui/core/Fab";
import LayersIcon from "@material-ui/icons/Layers";
import Button from "../Button/Button";
import {
  TIMER_START,
  TIMER_STOP,
  CAPTURE,
  CAPTURE_STOP,
  LAYOUT,
} from "../../utils/CONST_events";

import { connect } from "react-redux";
import { changeLayout } from "../../redux/actions/timerActions";

function ThirdColumn(props) {
  const [input, setInput] = useState("");

  const onTimerChange = (event) => {
    if (event.target.value.length <= 5) {
      setInput(event.target.value);
    }
    return;
  };
  return (
    <div className={classes.containerItem + " " + classes.col3}>
      <h1 className={row0}>Стейдж монитор</h1>
      <div className={row1}>
        <TextField
          className={item1}
          id="time"
          label="Время"
          value={input}
          onChange={onTimerChange}
        />

        <div className={item2}>
          <Button
            color="primary"
            data={{
              type: TIMER_START,
              minutes: input.split(":")[0],
              seconds: input.split(":")[1],
            }}
          >
            Tаймер старт
          </Button>
          <Button
            data={{
              type: TIMER_STOP,
            }}
          >
            Стоп
          </Button>
        </div>
      </div>
      <hr className={divider} />
      <div className={row2}>
        <Button
          data={{
            type: CAPTURE,
          }}
        >
          Начать общий отсчет
        </Button>
        <Button
          data={{
            type: CAPTURE_STOP,
          }}
        >
          Остановить общий отсчет
        </Button>
      </div>
      <Fab
        size="small"
        color="primary"
        aria-label="add"
        className={classes.fab}
        onClick={props.changeLayout.bind(this, { type: LAYOUT })}
      >
        <LayersIcon />
      </Fab>
    </div>
  );
}

const mapStateToProps = (state) => ({});

const mapActionsToProps = {
  changeLayout,
};

export default connect(mapStateToProps, mapActionsToProps)(ThirdColumn);
