import React from "react";
import ElementGenerator from "../../classes/ElementGenerator";

// redux
import { connect } from "react-redux";
import { handleAddColumn } from "../../redux/actions/UiActions";

// @material-ui/icons
import AddIcon from "@material-ui/icons/Add";

import classes from "../../assets/css/App.module.css";
import { divider } from "../../assets/css/Column.module.css";

// @material-ui/core
import Fab from "@material-ui/core/Fab";

function FirstColumn(props) {
  return (
    <div className={classes.containerItem + " " + classes.col1}>
      <h1>Видео</h1>
      {props.columnOne.map((element) => {
        return ElementGenerator[element.type](element.info);
      })}
      <Fab
        size="small"
        color="primary"
        aria-label="add"
        className={classes.fab}
        onClick={props.handleAddColumn}
      >
        <AddIcon />
      </Fab>
    </div>
  );
}
const mapStateToProps = (state) => ({
  columnOne: state.columnOne,
});

const mapActionsToProps = {
  handleAddColumn,
};

export default connect(mapStateToProps, mapActionsToProps)(FirstColumn);
